import React from "react";
import PropTypes from "prop-types";

const DEFAULT_THEMES = ["light", "dark"];

const ThemeContext = React.createContext({
  theme: "light",
  setTheme: () => {},
  toggleTheme: () => {},
  autoTheme: false,
  setAutoTheme: () => {},
  availableThemes: DEFAULT_THEMES,
});

export const useTheme = () => {
  const context = React.useContext(ThemeContext);
  if (!context) {
    throw new Error("useTheme must be used within a ThemeProvider");
  }
  return context;
};

export const ThemeProvider = ({
  children,
  defaultTheme = "light",
  identifier = "lebify-theme",
  autoTheme: initialAutoTheme = false,
  morningHour = 6,
  eveningHour = 18,
  themes = DEFAULT_THEMES,
}) => {
  // Merge and validate themes
  const availableThemes = React.useMemo(() => {
    // Start with provided themes, then add defaults if not present
    const allThemes = [...new Set(themes)];
    DEFAULT_THEMES.forEach((theme) => {
      if (!allThemes.includes(theme)) {
        allThemes.push(theme);
      }
    });

    if (!allThemes.includes(defaultTheme)) {
      console.warn(
        `Default theme "${defaultTheme}" not found in themes. Adding it.`
      );
      allThemes.push(defaultTheme);
    }

    return allThemes;
  }, [themes, defaultTheme]);

  // Theme state
  const [theme, setTheme] = React.useState(() => {
    if (typeof window !== "undefined") {
      const savedTheme = localStorage.getItem(identifier);
      return savedTheme && availableThemes.includes(savedTheme)
        ? savedTheme
        : defaultTheme;
    }
    return defaultTheme;
  });

  // Auto theme state
  const [autoTheme, setAutoTheme] = React.useState(initialAutoTheme);

  // Handle auto theme changes
  React.useEffect(() => {
    if (!autoTheme) return;

    const updateThemeByTime = () => {
      const currentHour = new Date().getHours();
      const newTheme =
        currentHour >= morningHour && currentHour < eveningHour
          ? "light"
          : "dark";
      setTheme(newTheme);
    };

    updateThemeByTime();
    const interval = setInterval(updateThemeByTime, 60000);
    return () => clearInterval(interval);
  }, [autoTheme, morningHour, eveningHour]);

  // Update DOM and storage when theme changes
  React.useEffect(() => {
    if (typeof window === "undefined") return;

    document.documentElement.setAttribute("data-theme", theme);
    document.body.setAttribute("data-theme", theme);

    if (!autoTheme) {
      localStorage.setItem(identifier, theme);
    }
  }, [theme, identifier, autoTheme]);

  // Toggle theme function
  const toggleTheme = React.useCallback(() => {
    if (autoTheme) {
      setAutoTheme(false);
    }

    setTheme((current) => {
      const currentIndex = availableThemes.indexOf(current);
      const nextIndex = (currentIndex + 1) % availableThemes.length;
      return availableThemes[nextIndex];
    });
  }, [autoTheme, availableThemes]);

  const contextValue = React.useMemo(
    () => ({
      theme,
      setTheme,
      toggleTheme,
      autoTheme,
      setAutoTheme,
      availableThemes,
    }),
    [theme, toggleTheme, autoTheme, availableThemes]
  );

  return (
    <ThemeContext.Provider value={contextValue}>
      {children}
    </ThemeContext.Provider>
  );
};

ThemeProvider.propTypes = {
  children: PropTypes.node.isRequired,
  defaultTheme: PropTypes.string,
  identifier: PropTypes.string,
  autoTheme: PropTypes.bool,
  morningHour: PropTypes.number,
  eveningHour: PropTypes.number,
  themes: PropTypes.arrayOf(PropTypes.string),
};

export default ThemeProvider;
